.success {
  color: rgb(30, 87, 25);
  background: rgb(235, 233, 233);
  font-size: 20px;
  border-style: solid;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
}

.error {
  color: rgb(179, 35, 25);
  background: rgb(235, 233, 233);
  font-size: 20px;
  border-style: solid;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
}